.App {
     color: white;
     justify-content: space-between;
     display: flex;
     flex-direction: row-reverse;
 }

 .card-right {
    background-color: white;
    width: 400px;
    background-image: url('./images//image-header-desktop.jpg');
    position: relative;
    background-size: cover;
    background-position: center;
}

.card-right::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(6, 6, 189, 0.347);
    pointer-events: none;
} 

.card-left {
    padding: 30px 30px 40px 30px;
    background-color: hsl(244, 38%, 16%);
    border-radius: 0px 0px 10px 10px;
    width: 350px;
}

span {
    color: hsl(277, 64%, 61%);
}

.details {
    font-weight: lighter;
    width: 320px;
    font-size: 15px;
    color: rgba(255, 255, 255, 0.673);

}

.reports {
    display: flex;
    justify-content: space-between;
    width: 310px;
}

.report span {
    font-size: large;
    font-weight: bold;
    color: white;
}

.re-data {
    font-weight: lighter;
    font-size: small;
    color: rgba(255, 255, 255, 0.673);
    letter-spacing: 2px;
    font-weight: 400;
}





 /* Extra small devices with 600px and below.  */
 @media only screen and (max-width: 375px) {

    .App {
        flex-direction: column;
        display: block;
        border-radius: 10px;
        text-align: center;
    }

    .card-right {
        width: 350px;
        height: 300px;
        border-radius: 10px 10px 0px 0px;
        text-align: center;
    }

    .card-left {
        width: auto;
    }
      
    .Title {
        font-size: 30px;
    }
   
    .details {
        width: auto;
        font-size: 15px;
        line-height: 2.0;
    }

    .reports {
        width: auto;
        display: block;
        margin-top: 30px;
    }

 }